import React, { useEffect, useRef, useState } from 'react';

type IWindowProps = {
    url: string,
    title: string,
    width: number,
    height: number
};

type IPopupProps = IWindowProps & {
    onClose: () => void,
    onCode: (response: any) => void,
    children: React.ReactNode
};

const createPopup = ({ url, title, height, width }: IWindowProps) => {
    const left = window.screenX + (window.outerWidth - width) / 2;
    const top = window.screenY + (window.outerHeight - height) / 2.5;
    const externalPopup = window.open(url, title, `width=${width},height=${height},left=${left},top=${top}`);
    return externalPopup;
};

const OauthPopup: React.FC<IPopupProps> = ({
    title = '',
    width = 500,
    height = 500,
    url,
    children,
    onCode,
    onClose
}: IPopupProps) => {
    const [externalWindow, setExternalWindow] = useState(Window | null);
    const intervalRef = useRef(0);

    const clearTimer = () => {
        window.clearInterval(intervalRef.current);
    };

    const onContainerClick = () => {
        setExternalWindow(
            createPopup({
                url,
                title,
                width,
                height
            })
        );
    };

    useEffect(() => {
        if (externalWindow) {
            intervalRef.current = window.setInterval(async () => {
                try {
                    const currentURL = externalWindow.location.href;
                    const params = new URL(currentURL).searchParams;
                    const code = params.get('code');
                    if (!code) {
                        return;
                    }
                    onCode(params);
                    clearTimer();
                    externalWindow.close();
                } catch (error) {
                    // eslint-ignore-line
                } finally {
                    if (!externalWindow || externalWindow.closed) {
                        onClose();
                        clearTimer();
                    }
                }
            }, 700);
        }
        return () => {
            if (externalWindow) externalWindow.close();
            if (onClose) onClose();
        };
    }, [externalWindow]);

    return (
        <div
            style={{ display: 'inline' }}
            onClick={() => {
                onContainerClick();
            }}
        >
            {children}
        </div>
    );
};

export default OauthPopup;
